// common mixins - start

@mixin qodefBckImageStyle() {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}

@mixin qodefImageOverlayHoverStyle($with-hover: true) {
    
    @if ($with-hover) {
        
        &:hover {
            
            &:after {
                opacity: 1;
            }
        }

        &:after {
            @include qodefAbsoluteHolderLayout();
            content: '';
            background-color: rgba($default-heading-color, .4);
            opacity: 0;
            @include qodefTransition(opacity .2s ease-in-out);
        }

    } @else {
        @include qodefAbsoluteHolderLayout();
        content: '';
        background-color: rgba($default-heading-color, .4);
        opacity: 0;
        @include qodefTransition(opacity .2s ease-in-out);
    }
}

@mixin qodefStandardPaginationStyle($list_type: null) {
    @include qodefRelativeHolderLayout(top);
    clear: both;

	@if ($list_type == 'shop') {
		margin: 0;
		
		ul {
			text-align: right
		}
		
	} @else if ($list_type == 'blog') {
		ul {
			text-align: left
		}
		
	} @else {
		margin: 40px 0 0;
	}

    ul {
        @include qodefRelativeHolderLayout(top);
        padding: 0;
        margin: 0;
        list-style: none;

        li {
            position: relative;
            display: inline-block;
            vertical-align: top;
            margin: 0 12px;
	        font-weight: bold;

            a {
                position: relative;
                display: inline-block;
                vertical-align: top;
                margin: 0;
                padding: 0;
	            color: $default-text-color;
	            
	            &:hover {
		            color: $default-heading-color;
	            }
            }
	
	        span {
		        color: $default-heading-color;
	        }

            &.qodef-pag-active {
                
                a {
                    color: $default-heading-color;
                }
            }

            &.qodef-pag-prev,
            &.qodef-pag-next,
            &.qodef-pag-first,
            &.qodef-pag-last {
                margin: 0 2px;

                a {
                    font-size: 24px;

                    span {
                        display: block;
                        line-height: 0.8em;

                        &:before {
                            display: block;
                            line-height: inherit;
                        }
                    }
                }
            }

            @if ($list_type == 'shortcode') {
                
                &.qodef-pag-prev {
                    
                    a {
                        opacity: 0;
                    }
                }

                &.qodef-pag-next {
                    
                    a {
                        opacity: 1;
                    }
                }

            } @else if ($list_type == 'shop') {
                span {
                    position: relative;
                    display: inline-block;
                    vertical-align: top;
                    margin: 0;
                    padding: 0;
                    color: $first-main-color;
                }

                a {
                    
                    &.next,
                    &.prev {
                        font-size: 0;
                        line-height: 0;

                        &:before {
                            display: block;
                            font-family: 'IonIcons'; // same icon pack as in our templates for pagination
                            font-size: 16px;
                            line-height: 29px;
                            -webkit-font-smoothing: antialiased;
                            -moz-osx-font-smoothing: grayscale;
                        }
                    }

                    &.prev {
                        margin-right: -10px;

                        &:before {
                            content: "\f3cf";
                        }
                    }

                    &.next {
                        margin-left: -10px;

                        &:before {
                            content: "\f3d1";
                        }
                    }
                }
            }
        }
    }
}

@mixin qodefButtonDefaultStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    width: auto;
    margin: 0;
    font-family: inherit;
    font-size: 16px;
    line-height: 2em;
    letter-spacing: 0;
    font-weight: 700;
    text-transform: capitalize;
    outline: none;
    box-sizing: border-box;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    @include qodefTransition(color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out);
}

@mixin qodefButtonSize($size: medium) {
    
    @if ($size == 'small') {
        padding: 7px 40px;

    } @else if ($size == 'medium') {
        padding: 10px 51px;

    } @else if ($size == 'large') {
        padding: 12px 58px;

    } @else if ($size == 'huge') {
        display: block;
        width: 100%;
        text-align: center;
        padding: 13px 26px;
    }
}

@mixin qodefButtonTransparentColor() {
    color: $default-text-color;
    background-color: transparent;
}

@mixin qodefButtonSolidColor($important: null) {
    color: #fff $important;
    background-color: $default-heading-color $important;
    border: 1px solid transparent $important;
}

@mixin qodefButtonSolidHoverColor($important: null) {
    color: $default-heading-color $important;
    background-color: transparent $important;
    border-color: #ccc;
}

@mixin qodefButtonOutlineColor($important: null) {
    color: $default-heading-color $important;
    background-color: transparent $important;
    border: 1px solid #ccc $important;
}

@mixin qodefButtonOutlineHoverColor($important: null) {
    color: #fff $important;
    background-color: $dark-gray-hover-color $important;
    border-color: $dark-gray-hover-color $important;
}

@mixin qodefPlaceholder {
    &::-webkit-input-placeholder {
        @content
    }

    &:-moz-placeholder {
        @content;

        opacity: 1;
    }
    &::-moz-placeholder {
        @content;

        opacity: 1;
    }
    &:-ms-input-placeholder {
        @content
    }
}

@mixin qodefMetaStyle {
	font-size: 15px;
	
	a {
		color: inherit;
	}
}

@mixin qodefBeforeText() {
    content: '';
    width: 11px;
    height: 11px;
    background-color: #ff2a0e;
    display: inline-block;
    margin-right: 15px;
    vertical-align: middle;
}

@mixin qodefRowText() {
    position: absolute;
    left: 50%;
    @include qodefTransform(translateX(-50%));
    font-size: 361px;
    font-weight: 700;
    line-height: 1em;
    letter-spacing: -10px;
    color: rgba($default-heading-color, 0.05);
    user-select: none;
}

// common mixins - end

