// mixins styles - start

%input-style {
    position: relative;
    width: 100%;
    margin: -2px 0 $input-margin;
    padding: $input-vertical-padding $input-horizontal-padding;
    font-family: inherit;
    font-size: 16px;
    font-weight: inherit;
    line-height: calc(#{$input-height} - (#{$input-vertical-padding} * 2) - 2px);
    color: $default-heading-color;
    background-color: transparent;
    border: 1px solid #bababa;
    border-radius: 0;
    outline: 0;
    cursor: pointer;
    -webkit-appearance: none;
    box-sizing: border-box;
    @include qodefTransition(border-color 0.2s ease-in-out);

    &:focus {
        color: $default-heading-color;
        border-color: $default-heading-color;
    }

    @include qodefPlaceholder {
        color: inherit;
    }
}

// mixins styles - end

